@import url(https://fonts.googleapis.com/css2?family=Bangers&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;1,100;1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@300&display=swap);
.header {
    height: 13vh;
    width: 100vw;
    background-color: white;
    color: black;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    padding: 1rem;
    font-family: 'Bangers', cursive;

}

.logo-container {
    display: -webkit-flex;
    display: flex;
    text-align: center;
    -webkit-align-items: center;
            align-items: center;
}

.header img {
    width: 3.5rem;
    height: 3.5rem;
}
.home {
    height: 87vh;
    width: 100vw;
    background-color: rgb(182, 182, 182);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    padding-top: 5rem;
}

h1 {
    font-family: 'Righteous', cursive;
}
.test-btn {
    background-color: black;
    color: white;
    height: 3.5rem;
    width: 10rem;
    border: none;
    border-radius: 5px;
    margin-top: 3rem;
}



.popup-box {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.334);
    position: fixed;
    top: 13vh;
    left: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-family: 'Roboto', sans-serif;
}

.box {
    width: 30rem;
    height: 20rem;
    background-color: white;
    border-radius: .6rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    box-shadow: 2px 2px 10px black;
}

.box h1 {
    padding-bottom: 2rem;
}

.box h3 {
    margin: 0.3rem;
}

.box button {
    margin: 1rem;
    background-color: black;
    color: white;
    padding: 0.3rem 0.5rem;
    border-style: none;
    border-radius: 0.1rem;
}
.test-box {
    width: 80vw;
    height: 70vh;
    background-color: white;
    display: -webkit-flex;
    display: flex;
    padding: 1rem;
}

.test-text {
    width: 50%;
    height: 100%;
    background-color: rgb(47, 47, 47);
    margin-right: 0.5rem;
    box-sizing: margin-box;
    color: white;
    padding: 0.5rem;
    font-family: 'Roboto Mono', monospace;
    font-size: small;
    overflow: hidden;
}

.typing-area {
    width: 50%;
    height: 100%;
    margin-left: 0.5rem;
    border: black 2px solid;
    padding: 0.5rem;
    cursor: default;
} 

.correct {
    color: rgb(0, 255, 0);
}

.incorrect {
    color: red;
}

.not-attempted {
    color: white;
}
.timer {
    width: 3rem;
    height: 2.5rem;
    margin: 1rem;
    background-image: linear-gradient(to bottom, rgba(61, 58, 58, 0.9)0% 50%,
    rgba(99, 93, 93, 0.9)50% 100%);
    color: white;
    font-family: 'Orbitron', sans-serif;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}
.result-card {
    width: 6rem;
    height: 2.8rem;
    background-color: white;
    margin: 1rem;
    text-align: center;
    padding: 0.3rem;
    font-family: 'Inconsolata', monospace;
    padding: 0.5rem;
    background-image: linear-gradient(to bottom, rgba(46, 46, 46, 0.9)0% 56%,
    rgba(235, 235, 235, 0.9)56% 100%);
    color: white;
}

.result-card h3 {
    font-weight: 300;
    color: black;
}
.test-container {
    height: 87vh;
    width: 100vw;
    background-color: rgb(182, 182, 182);
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.test-container-top {
    display: -webkit-flex;
    display: flex;
}
* {
    padding: 0rem;
    margin: 0rem;
    box-sizing: border-box;
}
